import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-todo',
  templateUrl: './todo.component.html',
  styleUrls: ['./todo.component.css']
})
export class TodoComponent implements OnInit {

  constructor() { 

  	if(localStorage.a)
  	{
  		this.todos=JSON.parse(localStorage.a)
  	}

  }

  ngOnInit() {
  }

  todos=[{title:"new angular upload",content:"on github upload it"} ]
  ob={index:-1,title:"---",content:"---"}
  x=true
  s=""
  column="title"
  toggle=false
  editstatus=false

  addNewTodo(a1,a2)
  {

  	let title=a1.value
  	let content=a2.value
  	this.todos.push({title:title,content:content})
  	a1.value=""
  	a2.value=""
  	this.save()
  	this.editstatus=false
  }

  todoDelete(i)
  {
  	this.todos.splice(i,1)
  	this.save()
  	this.editstatus=false
  }
  editTdodo(i)
  {
  	this.editstatus=true
  	this.ob.index=i
  	this.ob.title=this.todos[i].title
  	this.ob.content=this.todos[i].content
  	this.x=false
  }
  UpdateTodo(e1,e2,e3)
  {
  	let i=e1.value
  	let title=e2.value
  	let content=e3.value
  	this.todos[i].title=title
  	this.todos[i].content=content
  	this.editstatus=false
  }
  save()
  {
  	localStorage.a=JSON.stringify(this.todos)
  }
  todoSort(col)
  {
    this.column=col
    this.toggle=!this.toggle
  }
}
