import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sort'
})
export class SortPipe implements PipeTransform {

  transform(a,c,t): any {
    let b=a
    if(t)
    {
    	b=b.sort((arr1,arr2)=>{
    		if(arr1[c]<arr2[c])
    		{
	    		return -1
    		}
    		else
    		{
	    		return 1
    		}
    		return 0
    	})

    }
    else
    {
    		    	b=b.sort((arr1,arr2)=>{
    		    		if(arr1[c]<arr2[c])
    		    		{
    			    		return 1
    		    		}
    		    		else
    		    		{
    			    		return -1
    		    		}
    		    		return 0
    		    	})
    		    	
    }
    return b
  }

}
